<template>
  <kirby-field v-bind="$props">
    <kirby-select-input v-model="data" v-bind="$props" />
  </kirby-field>
</template>

<script>

import Props from '../Field.props.js';

export default {
  mixins: [Props],
  props: {
    label: {
      default: 'Select'
    },
    name: {
      default: 'select'
    },
    icon: {
      default: 'angle-down'
    },
    options: {
      type: Array
    }
  },
  data() {
    return {
      data: this.value
    }
  },
  watch: {
    data (value) {
      this.$emit('input', value)
    }
  }
}

</script>



